import React,{useEffect, useContext} from 'react';
import './AboutPage.component.scss';
import LayoutPage from '../../components/LayoutPage/LayoutPage.component.jsx';
import LayoutHorizontal from '../../components/LayoutHorizontal/LayoutHorizontal.component.jsx';
import Carousel from '../../components/Carousel/Carousel.component.jsx';
import SectionH2 from '../../components/SectionH2/Sectionh2.component.jsx';
import Paragraph from '../../components/Paragraph/Paragraph.component.jsx';
import { motion, useAnimation } from 'framer-motion';
import { AppContext } from '../../AppContext';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';
import CarouselInfo from '../../components/CarouselInfo/CarouselInfo.component.jsx';

export default function AboutPage() {
  const { 
    isXL,
    images,
    imageIndexObj
  } = useContext(AppContext);
  const [imageIndex, setImageIndex] = imageIndexObj;
  const showContent = useAnimation();

  useEffect(() => {
      const app = document.getElementById('app');
      const carouselInfo = document.getElementById('carousel-info');
      const scrollIndicator = document.getElementById('scroll-indicator');
      let lastScrollTop = 0;

      app.addEventListener(
        'scroll',
        () => {
          const position = window.pageYOffset || app.scrollTop;

          if (position > lastScrollTop) {
              app.addEventListener('scroll', () => {
                showContent.start(
                   isXL ? {x: '60vw'} : {x: '0vw'}
                )
                carouselInfo.style.display = 'none';
                scrollIndicator.style.display = 'none';
              })
          } else {
            app.addEventListener('scroll', () => {
              showContent.start({
                x: '100vw'
              })
              carouselInfo.style.display = 'flex';
              scrollIndicator.style.display = 'flex';
            })
          }
          lastScrollTop = position <= 0 ? 0 : position;
        },
        { passive: true },
      );
  }, [isXL, showContent]);

  return (
    <LayoutHorizontal>
      <div className="dummy"></div>
      <Carousel />
      <motion.section className="about-page-content" id="about-page-content" animate={showContent} initial={{x: '100vw'}}>
        <div className="about-page-container">
          <div className="about-page-header">
              <SectionH2 h2={'ISR Technology'} />
            </div>
            <div className="about-page-body">
              <Paragraph
                paragraph={
                  'ISR Technology provides services and solutions for business of all sizes. We are your single point of contact for all your IT needs. We offer everything from managed IT to audio visual and everything in between. Our dedicated staff will keep you informed on every project from beginning to end. Reach out to discuss your next IT project. ISR Technology is ready for the future and ready to help.'
                }
              />
            </div>
        </div>
      </motion.section>
      <div className="scroll-indicator-socket">
        <ScrollDownIndicator/>
      </div>
      <div className="carousel-info-socket">
        <CarouselInfo description={images[imageIndex].service} id={images[imageIndex].id} length={images.length}/> 
      </div>
    </LayoutHorizontal>
  );
}