import React, { useContext, useEffect } from 'react';
import variables from '../../styles/_variables.module.scss';
import './Values.component.scss';
import Value from '../Value/Value.component.jsx';
// import Logo from '../../components/logo/logo.component.jsx';
import { AppContext } from '../../AppContext';
import { motion, useAnimation } from 'framer-motion';
// import logoSecondaryColorFull from '../../assets/logos/logoSecondaryColorFull.svg';
// import logoPrimaryColorFull from '../../assets/logos/logoPrimaryColorFull.svg';
// import DummyIcon from '../../assets/icons/values/assistanceIconLightBg.svg'

export default function Values() {
  const { 
    isMD,
    isXXL,
    valuesData,
    themeColorObj
   } = useContext(AppContext);
  const themeColor = themeColorObj[0];
  const animateLetter = useAnimation();
  const letters = [' f','u','t','u','r','e'];

  useEffect(() => {
    animateLetter.start({
      opacity: 1
    })
  }, [animateLetter]);
  
  return (
    <section className="values full-vh" id="values">
      {valuesData.map(value => (
        <Value
          key={value.id}
          icon={themeColor === 'light' ? value.icon.light : value.icon.dark}
          // icon={DummyIcon}
          text={value.title}
          x={value.coordonates.x}
          y={value.coordonates.y}
          animation={value.animation}
          id={value.title}
          second={value.rotate.second}
          third={value.rotate.third}
        />
      ))}
      <motion.div
        className="logo-value"
        animate={{
          scale: [null, 1.2, 1],
          transition: { duration: 10, repeat: Infinity },
        }}
      >
        {/* <Logo
          logo={
            themeColor === 'light'
              ? logoPrimaryColorFull
              : logoSecondaryColorFull
          }
          width={'auto'}
          height={isLG ? '64px' : '40px'}
          class={'logo-value'}
        /> */}
      </motion.div>
      <motion.h1
        className={"ready " + (isXXL ? "display1" : isMD ? "display2" : "display3")}
        style={
          themeColor === 'light'
            ? { color: variables.primaryColor }
            : { color: variables.secondaryColor }
        }
      >
        ready for <br /> the 
        {
          letters.map( (letter,i) => (
            <motion.span
              key={i}
              animate={animateLetter} 
              initial={{opacity: 0}} 
              transition={{delay: i * 0.15}}
            >
              {letter}
            </motion.span>
          ))
        }
      </motion.h1>
    </section>
  );
}
