import React from 'react';
import './CarouselInfo.component.scss';

export default function CarouselInfo({description, id, length}) {
  return (
    <div className="carousel-info" id="carousel-info">
            <p className="caption description">{description}</p>
            <p className="caption">
                <span>{id}</span> / {length}
            </p>
    </div>
  );
}
