import React, { useContext, useEffect } from 'react';
import './HomePage.component.scss';
import Values from '../../components/Values/Values.component.jsx';
import Portfolio from '../../components/Portfolio/Portfolio.component.jsx';
import Partners from '../../components/Partners/Partners.component.jsx';
import Contact from '../../components/Contact/Contact.component.jsx';
import CallButton from '../../components/CallButton/CallButton.component.jsx';
import { AppContext } from '../../AppContext';

export default function HomePage() {
  const { 
    themeColorObj,
    browserHistoryObj
  } = useContext(AppContext);
  const themeColor = themeColorObj[0];
  const browserHistory = browserHistoryObj[0];

  useEffect(() => {
    if (
      browserHistory[browserHistory.length - 2] &&
      browserHistory[browserHistory.length - 2].includes('/services')
    ) {
      const services = document.getElementById('services');
      services.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }, [browserHistory]);

  return (
    <main className="home-page">
      <Values themeColor={themeColor} />
      <Portfolio themeColor={themeColor} />
      <Partners />
      <Contact />
      <CallButton />
    </main>
  );
}
